import { __ } from '@wordpress/i18n';
import { upDateQueryParams } from '../utilities/updateQueryParams';

import './editor.scss';

export default function Edit({attributes, setAttributes}) {


	const handlePricesOrder = () => {
		const params = new URL(newUrl).searchParams;
		const queryParams = Object.fromEntries(params.entries());

		queryParams.order = queryParams.order === 'ASC' ? 'DESC' : 'ASC'
		queryParams.metakey = 'price'

		upDateQueryParams('metakey', queryParams.metakey);
		upDateQueryParams('order', queryParams.order);
	}

		return (
				<>
				<div className="card-list-head">
					<h3 className="card-list-head__total">70 productos en tu busqueda</h3>
					<div className="card-list-head__sort">
						<h4 className="card-list-head__sort-title">Ordenar por:
							<span
								className='card-list-head__sort-order'
								onClick={() => { handlePricesOrder() }}
								>{queryParams.order === 'ASC' ? 'Precio de mayor a menor' : 'Precio de menor a mayor'}</span>
						</h4>
					</div>
				</div>
				</>
		);
}




